import { Component, OnInit } from '@angular/core';
import {NodeModel, NodeTypes, TreeModel} from './models/tree-struct.model';
import {TreeService} from './services/tree.service';

@Component({
  selector: 'app-journey-editor',
  templateUrl: './journey-editor.component.html',
  styleUrls: ['./journey-editor.component.scss']
})
export class JourneyEditorComponent implements OnInit {
  journeyData: TreeModel;
  constructor(private treeService: TreeService) {
    this.addNodeToTree();
    this.removeNodeFromTree();
  }

  ngOnInit() {
    // TODO: load and convert json to TreeModel => journeyData
    if (!this.journeyData) {
      this.journeyData = new TreeModel(NodeTypes.trigger, {})
      this.journeyData.addNode(this.journeyData._root, NodeTypes.add);
    }
  }
  addNodeToTree(): void {
    this.treeService.addNode$.subscribe(({parentNode, nodeType}) => {
      this.journeyData.addNode(parentNode, nodeType);
    })
  }
  removeNodeFromTree(): void {
    this.treeService.removeNode$.subscribe(({parentNode, node}) => {
      this.journeyData.removeNode(parentNode, node)
    })
  }

}



