import { Component, Input, OnInit } from '@angular/core';
import {NodeModel, NodeTypes} from '../../models/tree-struct.model';
import {TreeService} from '../../services/tree.service';

@Component({
  selector: 'app-delete-button',
  templateUrl: './delete-button.component.html',
  styleUrls: ['./delete-button.component.scss']
})
export class DeleteButtonComponent implements OnInit {
  @Input() node: NodeModel;
  @Input() parentNode: NodeModel;
  confirmDelete = false;
  allowDelete = true;
  constructor(private treeService: TreeService) { }

  ngOnInit() {
  }
  checkCanDelete(): void {
    if (this.node.type === NodeTypes.condition) {
      this.allowDelete = this.node.children[0].children[0].children.length + this.node.children[1].children[0].children.length < 2
    } else {
      this.allowDelete = true;
    }
  }
  reverseCheckCanDelete(): void {
    this.allowDelete = true;
  }
  showConfirmDelete(): void {
    this.confirmDelete = true;
  }
  hideConfirmDelete(): void {
    this.confirmDelete = false;
  }
  actionDeleteNode(): void {
    this.treeService.removeNodeFromTree(this.parentNode, this.node)
  }
}



