import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ModalModule } from 'ngx-bootstrap/modal';
import { TreeService } from './services/tree.service';

import { JourneyEditorComponent } from './journey-editor.component';
import {JourneyEditorRouting} from './journey-editor.routing';
import { TreeComponent } from './components/tree/tree.component';
import { TriggerNodeComponent } from './components/node-tree/trigger-node/trigger-node.component';
import { AddNodeComponent } from './components/node-tree/add-node/add-node.component';
import { PopoverModule } from 'ngx-bootstrap/popover';
import { MailNodeComponent } from './components/node-tree/mail-node/mail-node.component';
import { DelayNodeComponent } from './components/node-tree/delay-node/delay-node.component';
import { ConditionNodeRootComponent } from './components/node-tree/condition-node-root/condition-node-root.component';
import { ConditionNodeChildComponent } from './components/node-tree/condition-node-child/condition-node-child.component';
import { DeleteButtonComponent } from './components/delete-button/delete-button.component';


@NgModule({
  imports: [
    CommonModule,
    JourneyEditorRouting,
    ModalModule.forRoot(),
    PopoverModule.forRoot()
  ],
  declarations: [
    JourneyEditorComponent,
    TreeComponent,
    TriggerNodeComponent,
    AddNodeComponent,
    MailNodeComponent,
    DelayNodeComponent,
    ConditionNodeRootComponent,
    ConditionNodeChildComponent,
    DeleteButtonComponent
  ],
  providers: [
    TreeService
  ]
})
export class JourneyEditorModule { }




