import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { NodeModel, NodeTypes } from '../models/tree-struct.model';

@Injectable()
export class TreeService {

  // Observable string sources
  private addNode = new Subject<any>();
  private removeNode = new Subject<any>();

  // Observable string streams
  addNode$ = this.addNode.asObservable();
  removeNode$ = this.removeNode.asObservable();

  // Service message commands
  addNodeToTree(parentNode: NodeModel, nodeType: NodeTypes) {
    this.addNode.next({
      parentNode,
      nodeType
    });
  }

  removeNodeFromTree(parentNode: NodeModel, node: NodeModel) {
    this.removeNode.next({
      parentNode,
      node
    });
  }
}



