import { Component, OnInit, Input, Output} from '@angular/core';
import {NodeModel} from '../../../models/tree-struct.model';

@Component({
  selector: 'app-trigger-node',
  templateUrl: './trigger-node.component.html',
  styleUrls: ['./trigger-node.component.scss']
})
export class TriggerNodeComponent implements OnInit {
  @Input()node: NodeModel;

  constructor() { }

  ngOnInit() {
    if (this.node.data && !this.node.data.type) {
      this.node.data = {
        type: 'join_the_list',
        text: 'Subscribers joins the list',
        recipients_id: 1
      }
    }
  }

}



