import { NgModule } from '@angular/core';
import {
  Routes,
  RouterModule
} from '@angular/router';

import { JourneyEditorComponent } from './journey-editor.component';

const routes: Routes = [
  {
    path: '',
    component: JourneyEditorComponent,
    data: {
      title: 'Journey'
    },
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JourneyEditorRouting {}



