export enum NodeTypes {
  trigger,
  delay,
  email,
  condition,
  condition_yes,
  condition_no,
  blank,
  add
}

export class NodeModel {
  type: NodeTypes;
  // parent: NodeModel;
  children: Array<NodeModel>;
  data: any;
  constructor(type: NodeTypes, children: Array<NodeModel> = [], data: any = {}) {
    this.type = type;
    // this.parent = parent;
    this.children = children;
    this.data = data;
  }
  canRemove(): boolean {
    if (this.type !== NodeTypes.condition) {
      return true;
    } else {
      if (this.children[0].children.length === 0 || this.children[1].children.length === 0) {
        return true;
      }
    }
    return false;
  }
  getType(): string {
    if (this.type === NodeTypes.trigger) {
      return 'trigger';
    } else if (this.type === NodeTypes.email) {
      return 'email';
    } else if (this.type === NodeTypes.delay) {
      return 'delay';
    } else if (this.type === NodeTypes.condition) {
      return 'condition';
    } else if (this.type === NodeTypes.condition_yes) {
      return 'condition_yes';
    } else if (this.type === NodeTypes.condition_no) {
      return 'condition_no';
    } else if (this.type === NodeTypes.add) {
      return 'add';
    } else {
      return 'blank';
    }
  }
}

export class TreeModel {
  _root: NodeModel;
  constructor(node_type: NodeTypes, node_data: any) {
    this._root = new NodeModel(node_type, [], node_data);
  }
  addNode(parentNode: NodeModel, nodeType: NodeTypes) {
    // default: add-node
    const addNode = new NodeModel(NodeTypes.add);
    let nodeToAdd;
    switch (nodeType) {
      case NodeTypes.delay:
        nodeToAdd = new NodeModel(NodeTypes.delay, [addNode]);
        break;
      case NodeTypes.email:
        nodeToAdd = new NodeModel(NodeTypes.email, [addNode]);
        break;
      case NodeTypes.condition:
        const condition_yes = new NodeModel(NodeTypes.condition_yes, [new NodeModel(NodeTypes.add)]);
        const condition_no = new NodeModel(NodeTypes.condition_no, [new NodeModel(NodeTypes.add)]);
        nodeToAdd = new NodeModel(NodeTypes.condition, [condition_yes, condition_no]);
        break;
      default:
        nodeToAdd = new NodeModel(NodeTypes.add, [], {});
        break;
    }
    if (parentNode.children.length > 0) {
      if (nodeType === NodeTypes.condition) {
        nodeToAdd.children[0].children[0].children = [...parentNode.children];
      } else {
        nodeToAdd.children[0].children = [...parentNode.children];
      }
    }
    parentNode.children = [nodeToAdd];
  }
  removeNode(parentNode: NodeModel, nodeToRemove: NodeModel) {
    if (nodeToRemove.type !== NodeTypes.condition) {
      parentNode.children = [...nodeToRemove.children[0].children]
    } else {
      parentNode.children = [...(nodeToRemove.children[0].children[0].children.length > 0 ? nodeToRemove.children[0].children[0].children : nodeToRemove.children[1].children[0].children)]
    }
  }
}



